.project__side_back {
  transform: rotateY(180deg);
  background-color: #fff;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 20px;
  padding: 20px;
}

.project:hover .project__side_back,
.project:focus-within .project__side_back {
  transform: rotateY(0deg);
}
